import React from 'react'
import { Link } from 'react-router-dom'

function Home() {
  return (
    <div className='home-container'>
      <h1>
            You got the trave plans, we got the trsvel vans.
      </h1>
      <p>
            Add adventure to your life by joining the #vanlife movement. Join the perfect van to make your perfect road trip.
      </p>
      <Link to="/vans" >Find your van</Link>
    </div>
  )
}

export default Home
